.bg_r{
  background-color: red;
}

.bg_b{
  background-color: blue;
}
/* popup */
.pc_popup{
  width: 100vw;
  height: 100vh;
  background-color: gray;
  text-align: center;
  color:white;
}
.popup_box{
  position: relative;
  transform: translate(-50%,-50%);
  left: 50%;
  top:40%;
  width: 50%;
  border:2px solid skyblue;
}
.popup_btn{
  position:absolute;
  right: 5%;
  top:5%;
}

.border{
  border:2px solid skyblue;
  width: 70%;
  margin: auto;
}
.popup_title{
  margin:20px;
}
.popup_content{
  width: 70%;
  margin:auto;
  padding:20px;
}

/* 공통 */

.pnc{
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.pnc_container{
  border: 2px solid skyblue;
  border-radius: 10px;
  width: 90%;
  margin:50px; 
}

.pnc_pros{
  display: flex;
  justify-content: space-around;
  margin:50px;
}

.pnc_cons{
  display: flex;
  justify-content: space-around;
  margin:50px;
}
.pnc_pnc{
  width: 150px;
  height: 100px;
  border-radius: 50%;
  line-height: 100px;
  border: 1px solid black;
  font-size: 2rem;
  font-weight: bolder;
  margin:auto;  
}
.list{
  width: 150px;
  height: 100px;
  border-radius: 50%;
  color:white;
  line-height: 100px;
  transform: scale(1);
  transition: all .5s;
}

.list:hover{
  transform: scale(1.2);
}

@media screen and (max-width:767px){
  .list{
    width: 75px;
    height: 50px;
    line-height: 50px;
  }
  .popup_box{

    top:50%;

  }
  
}