/* 공통 CSS */

.tech{
  width: 95vw;
  height: 100%;
  margin: 20px auto;
  
}
.tech_title{
  margin: 50px;
  font-size: 3rem;
  font-weight: bolder;
  text-align: center;
}
.container_item{
  margin: 50px 30px;
  display: flex;
  justify-content: left;
}
.item_title{
  flex-basis: 80px;
}
.item_grahp{
  margin-left: 100px;
  border: 3px solid skyblue;
  background-color: skyblue;
  width: 30%;
}

.container_item:hover{
  color:skyblue
}
.container_item:hover .item_grahp{
  color:black
}